.Mylive-main {
  padding-top: 60px;
  min-width: 1366px;
}
.Mylive-main .Mylive-pic {
  margin: 40px 26px 16px 26px;
}
.Mylive-main .Mylive-pic .center-content-search {
  margin-bottom: 30px;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common {
  padding: 0 10px 0 10px;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .ant-col-20 {
  margin-left: 20px;
  width: calc(100% - 200px);
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .paper-year {
  color: #2A283D;
  font-weight: bold;
  display: inline-block;
  text-align: center;
  height: 30px;
  line-height: 30px;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .paper-common-btn .ant-btn {
  border: none;
  background: none;
  margin: 0 10px 5px 0;
  background: #EFEFEF;
  border-radius: 2px;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .paper-common-btn .ant-btn.ant-btn-primary {
  width: 110px;
  height: 32px;
  background: #3BB9FF !important;
  border-radius: 2px;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .paper-common-btn .ant-btn.ant-btn-primary span {
  color: #fff !important;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .paper-common-btn .ant-btn span {
  font-size: 14px;
  color: #000;
  font-weight: 400;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .ant-radio-button-wrapper {
  color: #000;
  background: none;
  border: none;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .ant-radio-button-wrapper span {
  color: #2A283D;
  opacity: 0.6;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .ant-radio-button-wrapper::before {
  width: 0;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  background: #3BB9FF !important;
  border-radius: 2px;
  height: 26px;
  line-height: 26px;
  color: #fff;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .ant-radio-button-wrapper.ant-radio-button-wrapper-checked span {
  color: #fff !important;
  opacity: 1;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .actives {
  color: #2cbde6;
  border-color: #2cbde6;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .ant-divider-horizontal {
  margin: 16px 0;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .paper-common-search {
  text-align: right;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .paper-common-search .ant-input-affix-wrapper .ant-input {
  background: #F4F4F4;
  border-radius: 13.5px;
  height: 32px;
  opacity: 0.7;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .paper-common .paper-common-search .paper-common-totale {
  margin-left: 20px;
  color: #000000;
  opacity: 0.3;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .ant-card-body {
  padding: 30px 20px 0 20px;
}
.Mylive-main .Mylive-pic .center-content-search .center-content-paper .ant-row {
  margin-bottom: 30px;
}
.Mylive-main .Mylive-pic .Mylive-pic-content {
  display: block;
  margin: 10px 0 10px 0;
}
.Mylive-main .Mylive-pic .Mylive-pic-content .span {
  font-family: Microsoft YaHei;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120.7%;
  /* identical to box height, or 17px */
  color: #EE9859;
}
.Mylive-main .Mylive-pic .Mylive-pic-content .live-right .paper-common-search {
  text-align: right;
}
.Mylive-main .Mylive-pic .Mylive-pic-content .live-right .paper-common-search .ant-input-affix-wrapper .ant-input {
  background: #F4F4F4;
  border-radius: 13.5px;
  height: 32px;
  opacity: 0.7;
}
.Mylive-main .Mylive-pic .Mylive-pic-content .live-right .paper-common-search .paper-common-totale {
  margin-left: 20px;
  color: #000000;
  opacity: 0.3;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom {
  margin: 0px 0 10px 0;
  background: #fff;
  padding: 15px 0;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list {
  padding: 22px 30px;
  background: #F9F9F9;
  margin: 8px 0;
  display: flex;
  align-items: center;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .ant-btn {
  margin-left: 20px;
  width: 75px;
  height: 29px;
  padding: 0 5px;
  text-align: center;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .link-fh {
  line-height: 30px;
  cursor: pointer;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .but-link {
  width: auto;
  background: none;
  border: none;
  box-shadow: none;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .but-link img {
  margin-left: 10px;
  margin-top: -2px;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .butlink-active span {
  font-family: Microsoft YaHei;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 120.7%;
  color: #3BB9FF;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .release-live-on {
  background: #CECECE;
  color: #fff;
  border: 1px solid #CECECE;
  cursor: auto;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .release-live-active {
  background: #4392DB;
  border: 1px solid #1E5CA6;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .release-live-link {
  background: #226286;
  border: 1px solid #589ABF;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .ant-col:nth-of-type(1) {
  display: flex;
  align-items: center;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .ant-col:nth-of-type(1) .pic-img {
  position: absolute;
  left: 0;
  top: 50%;
  margin-top: -21.5px;
  width: 67px;
  height: 43px;
  margin-right: 22px;
  float: left;
  background: url(../../../assets/images/placeholder_bg.png) no-repeat;
  background-size: 100% 100%;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .ant-col:nth-of-type(1) .pic-img img {
  width: 100%;
  height: 100%;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .ant-col:nth-of-type(1) .left {
  float: left;
  width: 100%;
  padding-left: 90px;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .ant-col:nth-of-type(1) {
  display: flex;
  align-items: center;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .ant-col:nth-of-type(1) .pic-img {
  width: 67px;
  height: 43px;
  margin-right: 22px;
  background: url(../../../assets/images/placeholder_bg.png) no-repeat center;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .ant-col:nth-of-type(1) .pic-img img {
  width: 100%;
  height: 43px;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .ant-col:nth-of-type(2) {
  display: flex;
  justify-content: flex-end;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .title {
  width: 750px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .title h2 {
  font-size: 16px;
  color: #2A283D;
  opacity: 0.5;
  display: inline-block;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list .title .span {
  display: inline-block;
  font-family: Microsoft YaHei;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.5;
  margin: 0 10px;
  margin-bottom: 0.5em;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list p {
  color: #999;
  margin: 0;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .mylive-list p span {
  margin-right: 10px;
  color: #2A283D;
  opacity: 0.5;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .course-list-lh {
  padding: 0 20px;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .course-list-lh .course-list-wrap dl dt {
  height: 130px;
  position: relative;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .course-list-lh .course-list-wrap dl dt img {
  width: 100%;
  height: 100%;
  max-height: 140px;
  border-radius: 3px;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .course-list-lh .course-list-wrap dl dt > span {
  padding: 2px 6px;
  position: absolute;
  left: 10px;
  bottom: 10px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 2px;
  color: #fff;
  font-size: 12px;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .course-list-lh .course-list-wrap dl dt > span span {
  margin-right: 5px;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .course-list-lh .course-list-wrap dl dd {
  padding: 0 5px;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .course-list-lh .course-list-wrap dl dd h3 {
  font-weight: bold;
  font-size: 18px;
  margin: 12px 0;
  line-height: 100%;
  color: #2A283D;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .course-list-lh .course-list-wrap dl dd > span {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #2A283D;
  font-size: 14px;
  display: block;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .course-list-lh .course-list-wrap dl dd p {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .course-list-lh .course-list-wrap dl dd p > span {
  color: #2A283D;
  font-size: 14px;
  margin-right: 15px;
  vertical-align: middle;
}
.Mylive-main .Mylive-pic .Mylive-pic-bottom .course-list-lh .course-list-wrap dl dd p > span span {
  margin-right: 3px;
}
html {
  overflow-x: auto;
  overflow-y: auto;
}
